	props: {
		goodsInfo: {
			type: Object,
			default() {
				return {}
			}
		}
	},
	data () {
		return {
			
		}
	},
	methods: {
		tapCard() {
			// TODO 将该商品ID传给父级，也就是滚动列表组件
			// 父级传过来的函数名 ckGoodsCard
			this.$emit('ckgoods', this.goodsInfo)
			
		}
	},
	components: {
		adsubSymbols
	}
}	
